

//
//  SITE NAV DROPDOWNS
//––––––––––––––––––––––––––––––––––––––––––––––––––


//
//  TYPOGRAPHY
//––––––––––––––––––––––––––––––––––––––––––––––––––

$site-header-dropdown-font-size-md: $site-header-nav-font-size-md;
$site-header-dropdown-font-size-lg: $site-header-nav-font-size-lg;



//
//  LIST
//––––––––––––––––––––––––––––––––––––––––––––––––––


.site-header-nav__child-list {
  display: none;
}

@include respond-above(sm) {
  .site-header-nav__child-list {
    background: rgba($black, 0);
    box-shadow: 0 5px 30px rgba($black, .1);
    display: block;
    height: 0;
    overflow: hidden;
    position: absolute;
    transition: all .1s ease;
    width: 250px;
  }

  .site-header-nav__list-item {
    &:hover {
      .site-header-nav__child-list {
        background: rgba($black, .75);
        height: auto;
        padding: $gutter * .5;
      }
    }
  }
}

@include respond-above(md) {
  .site-header-nav__list-item {
    &:hover {
      .site-header-nav__child-list {
        padding: ($gutter * .75) ($gutter * .5);
      }
    }
  }
}


//
//  LIST ITEM
//––––––––––––––––––––––––––––––––––––––––––––––––––

// @include respond-above(sm) {
//   .site-header-nav__child-list__item {

//   }
// }


//
//  LINK
//––––––––––––––––––––––––––––––––––––––––––––––––––

@include respond-above(sm) {
  .site-header-nav__child-list__link {
    color: $white;
    display: block;
    font-size: $site-header-dropdown-font-size-md;
    font-weight: normal;
    padding: ($gutter * .5) $gutter;
    text-decoration: none;
    transition: all .2s ease;

    &:hover,
    &:active,
    &:focus {
      text-decoration: none;
    }

    &:hover {
      background: $white;
      color: $black;
    }
  }
}

@include respond-above(md) {
  .site-header-nav__child-list__link {
    font-size: $site-header-dropdown-font-size-lg;
  }
}
