

//
//  PAGE HEADER
//––––––––––––––––––––––––––––––––––––––––––––––––––

$page-header-padding-xs: ($gutter * 4) 0;
$page-header-padding-sm: ($gutter * 5) 0;
$page-header-padding-md: ($gutter * 11) 0 ($gutter * 6);
$page-header-padding-lg: ($gutter * 12) 0 ($gutter * 6);
$page-header-padding-xl: ($gutter * 14) 0 ($gutter * 6);


//
//  STYLES
//––––––––––––––––––––––––––––––––––––––––––––––––––

.page-header {
  background-image: url('/img/home-hero-bg.jpg');
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  padding: $page-header-padding-xs;
  position: relative;
  text-align: center;
}

@include respond-above(xs) {
  .page-header {
    padding: $page-header-padding-sm;
  }
}

@include respond-above(sm) {
  .page-header {
    padding: $page-header-padding-md;
  }
}

@include respond-above(md) {
  .page-header {
    padding: $page-header-padding-lg;
  }
}

@include respond-above(lg) {
  .page-header {
    padding: $page-header-padding-xl;
  }
}


//
//  LAYOUT
//––––––––––––––––––––––––––––––––––––––––––––––––––

.page-header__container {
  position: static;
}

.page-header__col {
  position: static;
}



//
//  TITLE
//––––––––––––––––––––––––––––––––––––––––––––––––––

.page-header__title {
  margin-bottom: 0;
}

@include respond-above(md) {
  .page-header__title {
    font-size: rem(96px);
  }
}


//
//  ICON
//––––––––––––––––––––––––––––––––––––––––––––––––––

// .page-header__icon {
  
// }



//
//  SUB-TITLE
//––––––––––––––––––––––––––––––––––––––––––––––––––

.page-header__sub-title {
  font-size: rem(21px);
  margin-bottom: 0;
  // margin-top: $gutter;
}

@include respond-above(sm) {
  .page-header__sub-title {
    font-size: rem(24px);
  }
}

@include respond-above(md) {
  .page-header__sub-title {
    font-size: rem(36px);
  }
}


//
//  SCROLL PROMPT
//––––––––––––––––––––––––––––––––––––––––––––––––––

.page-header__scroll-prompt {
  bottom: $gutter * .5;
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
  width: 24px;
}

@include respond-above(sm) {
  .page-header__scroll-prompt {
    bottom: $gutter * 2;
    width: 36px;
  }
}

.page-header__scroll-prompt__svg {
  fill: $white;
}



//
//  DARK BG
//––––––––––––––––––––––––––––––––––––––––––––––––––

.page-header {
  &.-has-dark-bg {
    background-color: $dark-bg-bg-color;
  }
}
